// 通过设置 margin-left 漂移到左侧 通过旋转变成巨型
// 最后一个元素 display none
.container {
    display: flex;
}

.item {
    // padding: 10px;
    background-color: #f6f8fa;
    flex-grow: 1;
    text-align: center;
    font-size: 12px;
    cursor: pointer;
    height: 32px;
    line-height: 32px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-in-out .2s;
    &::after {
        content: "";
        position: absolute;
        display: inline-block;
        margin-left: auto;
        width: 22px;
        height: 22px;
        // background-color: red;
        z-index: 1;
        // top: 1px;
        right: -11px;
        transform: rotate(45deg);
        background-color: #f6f8fa;
        transition: all ease-in-out .2s;
    }
    &:last-child::after {
        display: none;
    }
    &:hover {
        background-color: #ccf4d7;
        color: #00c735;
    }
}
.item:hover::after {
    background-color: #ccf4d7;
}
.active:hover::after {
    background-color: #00b831;
}

.active {
    color: #fff;
    background-color: #00b831;
    &::after {
        content: "";
        position: absolute;
        display: inline-block;
        margin-left: auto;
        width: 22px;
        height: 22px;
        z-index: 1;
        // top: 1px;
        right: -11px;
        transform: rotate(45deg);
        background-color: #00b831;

    }
    &:hover {
        background-color: #00b831;
        // color: ;
        color: #fff;
    }
}

.doneStyle {
    color: #00b831;
}